import React from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';

export default class MainInfo extends React.Component {

    render() {
        let contract = this.props.contract || {};
        let { registrationNumber, sum, contractCreateDate, createDateTime, fulfillmentDate } = contract;

        return (<table className="table">
                <tbody>
                    <tr className="first">
                        <td className="col-3"><b> :</b></td>
                        <td>{registrationNumber}</td>
                    </tr>
                    <tr>
                        <td><b> :</b></td>
                        <td>{sum} RUB(?)</td>
                    </tr>
                    <tr>
                        <td><b>:</b></td>
                        <td> </td>
                    </tr>
                    <tr>
                        <td><b>  :</b></td>
                        <td><i> </i></td>
                    </tr>
                    <tr>
                        <td><b>  :</b></td>
                        <td> createDateTime {createDateTime} </td>
                    </tr>
                    <tr>
                        <td><b>  :</b></td>
                        <td>{fulfillmentDate} fulfillmentDate?</td>
                    </tr>
                    <tr>
                        <td><b> :</b></td>
                        <td>{contractCreateDate} contractCreateDate</td>
                    </tr>
                    <tr>
                        <td><b>  :</b></td>
                        <td>7-2011</td>
                    </tr>
                    <tr>
                        <td><b> :</b></td>
                        <td>94-</td>
                    </tr>
                    <tr>
                        <td><b>  zakupki.gov.ru:</b></td>
                        <td>
                            <p><a href="http://zakupki.gov.ru/pgz/public/action/contracts/info/common_info/show?source=epz&amp;contractInfoId=1790813"> 
                                            </a></p>
                            <p><a href="http://zakupki.gov.ru/pgz/printForm?type=CONTRACT_INFO&amp;id=1790813"> 
                                            </a></p>
                        </td>
                    </tr>
                    <tr>
                        <td><b>  .json  :</b></td>
                        <td><a href="http://openapi.clearspending.ru/restapi/v3/contracts/get/?regnum=0335300036911000008" target="_blank">
                            </a></td>
                    </tr>
                    <tr>
                        <td><b> :</b></td>
                        <td>1 <a href="#suppliers">
                             <i className="fa fa-angle-down"></i></a></td>
                    </tr>
                </tbody>
            </table>);
    }
};


